export const string = "<div [ngStyle]=\"{position:active?'relative':null}\"><div [(elementHeightModel)]=\"elementHeightModel\" [ngClass]=\"wrapClass\" [class.no-scroll-bars]=\"active && !scrollBars && scrollBars!=null\" [ngStyle]=\"active ? {overflow:'auto',position:'absolute',width:'100%'} : {}\"><ng-content></ng-content></div><div *ngIf=\"active\" [ngStyle]=\"{'height.px':elementHeightModel}\" style=\"visibility:hidden;overflow:hidden;\">&nbsp;</div></div>"